
@media (hover: hover) and (pointer: fine) {
    a:hover { font-weight: bold; }
}

a { text-decoration: none; color: #529afa;
    margin-inline-start: 0.5em; margin-inline-end: 0.5em; letter-spacing: .15em; }

div { display: block; }

#root {
    text-align: center;
    letter-spacing: .2em;
    flex-direction: column;
    height: 100%;
    display: flex;
}

.projects {
    display: flex;
    flex-direction: column;
    font-size: .85em;

    width: 220px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -110px;
    margin-top: -220px;
}

.github {
    font-size: 1.6em;
    margin-block-end: 1.5em !important;
}
.return-to-home {
    margin-block-start: 3em;
    font-size: 1.3em;
}

.projects a {
    margin-block-end: 1em;
}

.content {
    width: 380px;
    height: 140px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -190px;
    margin-top: -190px;
}

h1 { margin-block-start: 1em; margin-block-end: 0em; margin-inline-start: 1em; margin-inline-end: 1em; }
p { margin-block-start: .5em; margin-block-end: 1em; margin-inline-start: 0em; margin-inline-end: 0em; }
